@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;500&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #8F9E8B;
  color: #292929;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 300;
}

button {
  display: inline-block;
  overflow: visible;
  border: none;
  text-transform: none;
  white-space: nowrap;
  cursor: pointer;
}

input,
button {
  font-family: inherit;
}
.app__main {
  padding: .5rem;
}

.app__section {
  max-width: 480px;
  margin: auto auto 1rem;
  padding: 1rem;
  border-radius: .75rem;
  background: #F2E6B6;
  box-shadow: 2px 2px 12px #5B7876;
}
.button {
  width: 100%;
  padding: .75rem;
  border-radius: 0.25rem;
  background: #8F9E8B;
  color: white;
  font-size: .75rem;
  letter-spacing: .07rem;
  text-transform: uppercase;
}

.button:hover {
  background: #5B7876;
}

.button:focus {
  border: #5B7876 solid 2px;
  outline: none;
  background: #5B7876;
}
.header {
  margin-bottom: .5rem;
  padding: 1rem;
  background: #292929;
  box-shadow: 2px 2px 12px #5B7876;
  color: white;
  text-align: center;
}

.header__title {
  margin: 0;
  color: #F2E6B6;
  letter-spacing: .1rem;
  text-transform: uppercase;
}
.add-item-section {
  padding-bottom: 1rem;
}

.add-item-form__label {
  font-size: .75rem;
  font-weight: 400;
  letter-spacing: .07rem;
  text-transform: uppercase;
}

.add-item-form__input {
  width: 100%;
  margin-bottom: .5rem;
  padding: .5rem;
  border: #8F9E8B solid 1px;
  border-radius: 0.25rem;
  font-size: 1.1rem;
  font-weight: 400;
}

.add-item-form__input:focus {
  border: #5B7876 solid 2px;
  outline: none;
}

.add-item-form__input::-webkit-input-placeholder {
  color: #F2E6B6;
}

.add-item-form__input:-ms-input-placeholder {
  color: #F2E6B6;
}

.add-item-form__input::placeholder {
  color: #F2E6B6;
}
.todo-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.todo-list__heading {
  margin-bottom: .5rem;
}

.todo-list__heading-done {
  -webkit-order: 3;
          order: 3;
  margin-top: 1rem;
}

.todo-list__heading-icon {
  color: #8F9E8B;
  vertical-align: text-top;
}
.todo-item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: stretch;
          align-content: stretch;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-order: 2;
          order: 2;
  margin-bottom: .5rem;
  padding: .5rem .5rem .25rem;
  border-radius: .25rem;
  background: #5B7876;
  color: white;
  text-decoration: none;
  cursor: pointer;
}

.todo-item--done {
  -webkit-order: 4;
          order: 4;
}

.todo-item:hover,
.todo-item--done {
  background: #8F9E8B;
  -webkit-text-decoration: white line-through wavy;
          text-decoration: white line-through wavy;
}

.todo-item--done:hover {
  background: #5B7876;
  color: white;
  text-decoration: none;
}

.todo-item>* {
  padding: .4rem .55rem;
}

.todo-item__content {
  -webkit-flex-grow: 10;
          flex-grow: 10;
}

.todo-item__content h3 {
  margin-bottom: 0;
}

.todo-item__content p {
  margin-top: .3rem;
  margin-bottom: 0;
  font-size: .75rem;
  letter-spacing: .07rem;
  text-transform: uppercase;
}

.todo-item__icon--delete:hover {
  border-radius: 50%;
  background: #8F9E8B;
}

.todo-item .todo-item__icon--done,
.todo-item:hover .todo-item__icon,
.todo-item--done .todo-item__icon {
  display: none;
}

.todo-item:hover .todo-item__icon--done,
.todo-item--done .todo-item__icon--done,
.todo-item--done:hover .todo-item__icon {
  display: inline;
}

.todo-item--done:hover .todo-item__icon--done {
  display: none;
}
